import React, { useState } from 'react';
import styled from 'styled-components';


const Layout = styled.div`
  display: flex;
  align-items: flex-start;
  padding: 20px;
  flex-direction: row-reverse;
  position:absolute;
  left:200px;
  top:600px;
`;

const ImageSection = styled.div`
  flex:2;
  padding: 20px;
`;



const FormSection = styled.div`
  flex: -1;
  padding: 20px;
  background: #f4f7f6;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
`;

const Title = styled.h2`
  margin-bottom: 20px;
  color: #4a5d56;
`;

const SelectGroup = styled.div`
  margin-bottom: 20px;
`;
const Select = styled.select`
  padding: 12px 40px ;
  margin-right: 25px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
`;

const Button = styled.button`
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #a0c4b8; // 부드러운 녹색
  color: white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  &:hover {
    background-color: #95b1a6;
  }
`;

const CostDisplay = styled.div`
  margin-top: 20px;
  font-size: 18px;
  font-weight: bold;
  color: #667776; // 진한 회색
  margin-top: 20px;
`;

const MoldingCal = ({ onCostChange }) => {
  const [selectedNumber, setSelectedNumber] = useState(2); // 초기 문 개수 설정
  const [selectedType, setSelectedType] = useState('top'); // 초기 시공 옵션 설정
  const [calculatedCost, setCalculatedCost] = useState(0);

  const calculateCost = () => {
    const slopeTop = 11.6;
    const interceptTop = 19.4;
    const slopeBottom = 8.5;
    const interceptBottom = 14.5;
    const slopeBoth = 19.9;
    const interceptBoth = 30.6;

    let cost = 0;
    if (selectedType === 'top') {
      cost = slopeTop * selectedNumber + interceptTop;
    } else if (selectedType === 'bottom') {
      cost = slopeBottom * selectedNumber + interceptBottom;
    } else if (selectedType === 'both') {
      cost = slopeBoth * selectedNumber + interceptBoth;
    }
    const calculatedCost = cost.toFixed(2); // 계산된 비용을 소수점 둘째자리까지 반올림
    setCalculatedCost(calculatedCost);
    onCostChange(calculatedCost);
  };

  return (
    <Layout>
    <ImageSection>
    </ImageSection>
    <FormSection>
      <Title>문 몰딩 비용 계산기</Title>
      <SelectGroup>
        <Select onChange={e => setSelectedNumber(e.target.value)} value={selectedNumber}>
          {[2, 3, 4, 5, 6].map(number => (
            <option key={number} value={number}>{number}개(문)</option>
          ))}
        </Select>
        <Select onChange={e => setSelectedType(e.target.value)} value={selectedType}>
          <option value="top">상단 몰딩</option>
          <option value="bottom">하단 몰딩</option>
          <option value="both">상단+하단 몰딩</option>
        </Select>
        </SelectGroup>
        <Button onClick={calculateCost}>비용 계산</Button>
        <CostDisplay>장판 비용: {calculatedCost}만원</CostDisplay>
      </FormSection>
    </Layout>

  );
};

export default MoldingCal;
