import React, { useState, useEffect } from 'react';
import FloorCal from './FloorCal';
import Wallpaper from './Wallpaper'
import HdfCal from './HdfCal';
import MoldingCal from './MoldingCal';
import DoorCal from './DoorCal';
import styled from 'styled-components';
// 다른 시공 비용 계산 컴포넌트도 동일하게 임포트



const TotalCostDisplay = styled.div`
  margin-bottom: 20px;
  padding: 10px;
  background-color: #5A4CF7;
  border: 1px solid #ddd;
  text-align: center;
  font-size: 3em;
  color:white;
  font-weight: 700;
  
`;

const TotalCostCalculator = () => {
    const [totalCost, setTotalCost] = useState(0);
  const [costs, setCosts] = useState({
    floor: 0,
    wallpaper: 0,
    hdf:0,
    molding:0,
    door:0
      // 다른 시공 옵션에 대한 초기 비용 설정
    });
  
    const handleCostChange = (cost, type) => {
        setCosts(prevCosts => ({ ...prevCosts, [type]: parseFloat(cost) }));
      };
    
      useEffect(() => {
        const newTotalCost = Object.values(costs).reduce((sum, currentCost) => sum + currentCost, 0);
        setTotalCost(newTotalCost);
      }, [costs]);
    
      return (
        <div>
            <TotalCostDisplay>
        총 예상 비용: {totalCost.toFixed(2)}만원 ±α
      </TotalCostDisplay>
          <FloorCal onCostChange={(cost) => handleCostChange(cost, 'floor')} />
          <Wallpaper onCostChange={(cost) => handleCostChange(cost, 'wallpaper')} />
          <HdfCal onCostChange={(cost) => handleCostChange(cost, 'hdf')} />
          <MoldingCal onCostChange={(cost) => handleCostChange(cost, 'molding')} />
          <DoorCal onCostChange={(cost) => handleCostChange(cost, 'door')} />
      {/* 다른 컴포넌트들도 마찬가지로 추가 */}
      
    </div>
  );
};

export default TotalCostCalculator;
