import { useNavigate } from 'react-router-dom';
import React, { useState } from 'react';
import styled from 'styled-components';
import Wrapper from './conponent/Wrapper';
import TotalCostCalculator from './TotalCostCalculator';


const ButtonContainer = styled.div`
  position: absolute;
  width: 578px;
  height: 66px;
  top: 956px;
  left: 690px;
`;

const Button = styled.button`
  position: absolute;
  width: 576px;
  height: 66px;
  background-color: #4637F1;
  border-radius: 15px;
  border: none;
  cursor: pointer; // 버튼에 커서 추가
`;

const ButtonText = styled.div`
  position: absolute;
  top: 21px;
  left: 228px;
  font-family: 'Inter-SemiBold', Helvetica;
  font-weight: 600;
  font-size: 20px;
  color: white;
  white-space: nowrap;
`;
const CheckboxContainer = styled.div`
  position: absolute;
  top: 850px; // 버튼보다 약간 위에 위치
  left: 690px;
  width: 545px;
  padding: 15px;
  background-color: #f0f0f0; // 배경색
  font-size : 1rem;
  border-radius: 15px;
`;


const Interior = () => {
  const navigate = useNavigate();
  const [option1Checked, setOption1Checked] = useState(false);
  const [option2Checked, setOption2Checked] = useState(false);


  
 
  const handleOption1Change = (event) => {
    setOption1Checked(event.target.checked);
  };

  const handleOption2Change = (event) => {
    setOption2Checked(event.target.checked);
  };
  
  const isEligibleForConsultation = option1Checked && option2Checked;

  const handleConsultationClick = () => {
    if (!isEligibleForConsultation) {
      alert('모든 안내사항에 동의해주세요.'); // 체크박스 상태에 따른 alert
    } else {
      navigate('/Kakao'); // 체크박스가 모두 선택된 경우 페이지 이동
    }
  };


    return (
      <Wrapper>
      <div>
        {/* 안내사항 체크박스 */}
        <CheckboxContainer>
        <div>
          <input
            type="checkbox"
            checked={option1Checked}
            onChange={handleOption1Change}
          /> 
          동의 ㅣ 위 견적사항은 어디까지나 가견적이므로 실제 건축주의 요청과 현장상황에따라 비용이 가감될 수 있습니다. 동의부분에 체크해주세요!
        </div>
        <div>
          <input
            type="checkbox"
            checked={option2Checked}
            onChange={handleOption2Change}
          /> 
          동의 ㅣ 위 견적사항은 아파트/오피스텔을 예시로 견적을 산출하는것이라 빌라/주택일경우 비용이 가감될 수 있습니다. 동의부분에 체크해주세요!
        </div>
        </CheckboxContainer>
        
      </div>
      <TotalCostCalculator/>
      <ButtonContainer>
        <Button onClick={handleConsultationClick}> {/* 이제 항상 클릭 가능 */}
          <ButtonText>1:1 상담 받기</ButtonText>
        </Button>
      </ButtonContainer>


      </Wrapper>
      
    );
  };
  
  export default Interior;
  